.next-nonth {
  font-family: Arial, sans-serif;
  margin-top: 40px;
  border: 1px solid #172b4d;
  padding: 10px 10px 10px 20px;
  max-width: 330px;
  margin-inline-start: -100px;


  h2 {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }

  p {
    margin-bottom: 0.5rem;
  }

  label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }

  input[type="text"] {
    border: 1px solid #ccc;
    border-radius: 0.25rem;
    padding: 0.5rem;
    margin-bottom: 1rem;
    min-width: 90%;

    &:focus {
      outline: none;
      box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    }
  }

  button {
    margin-left: 15px;
    background-color: #4caf50;
    border: none;
    border-radius: 0.25rem;
    color: white;
    cursor: pointer;
    font-size: 1rem;
    font-weight: bold;
    padding: 0.5rem;
    margin-bottom: 1rem;
    transition: background-color 0.2s ease;

    &:hover {
      background-color: #3e8e41;
    }

    &:focus {
      outline: none;
      box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    }
  }

  p {
    margin-top: 1rem;
    font-weight: bold;
  }
}