.reminder-conteinor {

  .button-remind {
    background-color: #0077cc;
    border: none;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    font-size: 16px;
    margin-right: 10px;
    padding: 8px 16px;

    &:hover {
      background-color: #005da6;
    }

    &:active {
      background-color: #004d8c;
    }

    &:focus {
      outline: none;
    }
  }

  .reminder-popup {
    position: fixed;
    bottom: 50px;
    right: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    padding: 10px 30px 10px 20px;
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 1.2rem;
    border: 1px solid black;
    color: #333;

    &::before {
      content: '';
      display: block;
      position: absolute;
      left: -10px;
      bottom: 10px;
      border: 10px solid transparent;
      border-right-color: #fff;
    }
  }

  .close-popup {
    position: absolute;
    top: 10px;
    right: 10px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: 1.2rem;
    color: #777;

    &:hover {
      color: #333;
    }

    &:focus {
      outline: none;
    }
  }
}