.single-content.app {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
    padding: 20px;
    // border: 1px solid #ccc;
    border-radius: 5px;

    form {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;

        span {
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 10px;
        }

        label {
            font-size: 16px;
            margin-bottom: 10px;
        }

        button {
            padding: 10px 20px;
            font-size: 16px;
            font-weight: bold;
            background-color: #008CBA;
            color: #fff;
            border: none;
            border-radius: 5px;
            cursor: pointer;

            &:hover {
                background-color: #0073aa;
            }
        }
    }

    p {
        font-size: 16px;
        white-space: pre-wrap;
        text-align: center;
    }
}