.reminder-options {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    h2 {
      font-size: 1.5rem;
      font-weight: bold;
      margin-bottom: 1rem;
    }
    
    button {
      padding: 0.5rem 1rem;
      font-size: 1.2rem;
      background-color: #2d5e84;
      color: #fff;
      border: none;
      border-radius: 4px;
      margin-bottom: 1rem;
      
      &:hover {
        cursor: pointer;
        background-color: #3a6d9e;
      }
      
      &:last-child {
        margin-bottom: 0;
      }
    }
    
    button.active {
      background-color: #4c9c4c;
      
      &:hover {
        background-color: #58a958;
      }
    }
    
    button.set-reminder {
      font-size: 1.2rem;
      background-color: #e06060;
      
      &:hover {
        background-color: #f07171;
      }
    }
  }
  