.daf-yomi {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
    font-family: Arial, sans-serif;

    form {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;

        span {
            font-size: 24px;
            font-weight: bold;
            margin-bottom: 10px;
        }

        label {
            display: flex;
            align-items: center;
            margin: 10px 0;

            input {
                margin-right: 10px;
            }

            span {
                font-size: 18px;
            }
        }

        button {
            background-color: #007bff;
            color: #fff;
            font-size: 18px;
            padding: 10px;
            border: none;
            border-radius: 5px;
            cursor: pointer;

            &:hover {
                background-color: #0069d9;
            }
        }
    }

    p {
        font-size: 20px;
        margin: 20px;
    }
}