.jewish-convert-container {
    margin: 20px;

    .jewish-convert-label {
        display: block;
        font-weight: bold;
        margin-bottom: 5px;
    }

    .jewish-convert-note {
        font-style: italic;
        margin-bottom: 10px;
    }

    .jewish-convert-input {
        border: 1px solid #ccc;
        border-radius: 5px;
        padding: 5px;
        margin-bottom: 10px;
        font-size: 16px;
    }

    .jewish-convert-button {
        background-color: #007bff;
        color: #fff;
        border: none;
        border-radius: 5px;
        padding: 5px 10px;
        font-size: 16px;
        cursor: pointer;
    }

    .jewish-convert-button:hover {
        background-color: #0069d9;
    }

    .jewish-convert-result {
        margin-top: 20px;
        font-weight: bold;
    }
}